"use client";
import React, { useState } from "react";
import Leftnav from "../../components/Leftnav/leftnav"
import Mycourses from "../../components/MyCourses";

const home =()=>{

    return(
        <>
        <div className="flex h-screen overflow-hidden">
          {/* Left Navigation */}
          <div className="hidden md:block w-1/5 bg-gray-50 h-screen">
            <Leftnav />
          </div>
      
          {/* Main Content (MyCourses) */}
          <div className="w-full md:w-4/5 bg-white overflow-y-auto h-screen">
            <Mycourses />
          </div>
        </div>
      </>
      
      
    )
}

export default home